@import "variables";

.container {
  --padding-left: 14px;
  --padding-right: 15px;

  padding-left: var(--padding-left);
  padding-right: var(--padding-right);

  @media (min-width: $desktop_breakpoint) {
    max-width: $desktop_breakpoint;
    margin-left: auto;
    margin-right: auto;
  }
}