@mixin flex-row-layout {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
@mixin flex-column-layout {
    display: flex;
    flex-direction: column;
    align-items: center;
}


@mixin reset-list {
    margin: 0;
    padding: 0;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 0;
    list-style: none;
}